export var getTransformValue = function (transform) {
    var regex = /-?[0-9]*px/g;
    var match = transform.match(regex);
    if (match && match.length === 2) {
        var y = Number(match[0].split("px")[0]);
        var x = Number(match[1].split("px")[0]);
        return { y: y, x: x };
    }
    return { x: 0, y: 0 };
};
export var reorder = function (list, startIndex, endIndex) {
    var result = Array.from(list);
    var removed = result.splice(startIndex, 1)[0];
    result.splice(endIndex, 0, removed);
    return result;
};
